* {
    margin: 0;
    padding: 0;
    box-sizing: border-box;
}
body {
    font-family: Arial, sans-serif;
}
.navbar {
    display: flex;
    justify-content: space-between;
    align-items: center;
    background: #fff;
    padding: 15px;
    color: white;
    max-width: 1320px;
    margin: auto;
}

.menu {
    display: flex;
    list-style: none;
    gap: 39px;
    justify-content: center;
    text-transform: uppercase;
}
.menu li {
    margin: 0 10px;
    position: relative;
    display: flex;
    align-items: center;
}
.menu li a {
    color: #000;
    text-decoration: none;
    font-weight: 500;
    font-family: League Spartan, sans-serif;
    display: flex;
    align-items: center;
    gap: 10px;
}
.menu-toggle {
    display: none;
    font-size: 24px;
    cursor: pointer;
}
.dropdown {
    display: none;
    position: absolute;
    top: 50px;
    left: 0;
    list-style: none;
    backdrop-filter: blur(50px);
    background: #a9cdeb;
    border: 1px solid #59ecaa;
    padding: 0;
    border-radius: 10px;
    min-width: 160px;
    z-index: 99;
}
.dropdown li {
    padding: 0;
    margin: 0;
}
.dropdown li a {
    text-decoration: none;
    color: #000;
    display: flex;
    font-size: 14px;
    font-weight: 500;
    gap: 10px;
    padding: 14px 10px;
    text-align: left;
    width: 100%;
}
.dropdown li a:hover {
    background-color: #3035ab;
    color: #fff;
    border-radius: 5px;
}
span.user_icon {
    align-items: center;
    background: #3035ab;
    border-radius: 100px;
    color: #fff;
    display: flex;
    font-size: 16px;
    font-weight: 700;
    height: 35px;
    justify-content: center;
    line-height: 14px;
    text-transform: uppercase;
    width: 35px;
}

@media (max-width: 991px) {
    .menu {
        display: block;
        z-index: 99;
        position: fixed;
        top: 0;
        right: 0;
        height: 100vh;
        width: 96%;
        background: #13152b;
        transform: translateX(100%);
        transition: transform 0.3s ease-in-out;
        box-shadow: -5px 0px 10px rgba(0, 0, 0, 0.1);
        padding: 100px 50px 50px;
    }
    .menu.active {
        left: 0;
        transform: translateX(4%);
    }
    .menu-toggle {
        display: block;
        position: relative;
        z-index: 999;
        color: #000;
        
    }
    .header_outer img.logo {
        max-width: 230px;
    }
    .menu.active li a {
        color: #fff;
    }
    span.menu-toggle.active-icon {
        color: #fff;
    }
    .menu li {
        justify-content: center;
        margin-bottom: 40px;
    }
    .dropdown {
        right: 0;
        transform: translate(0%, 10px);
    }
    .dropdown li {
        margin: 0 !important;
    }
    .menu.active .dropdown li a {
        color: #000;
    }
    section.Toastify {
        display: none;
    }
    .menu.active:before {
        content: "";
        position: absolute;
        background: #000;
        width: 100%;
        height: 100%;
        left: -100%;
        right: 0;
        top: 0;
        bottom: 0;
        opacity: .5;
        z-index: -1;
    }
}

@media (max-width: 768px) {
    .menu.active {
        left: 0;
        transform: translateX(59%);
        width: 63%;
    }
}
@media (max-width: 820px) {
    .menu.active {
        left: 0;
        transform: translateX(59%);
        width: 63%;
    }
}
@media (max-width: 480px) {
    .menu.active {
        left: 0;
        transform: translateX(4%);
        width: 96%;
    }
   
}
@media (min-width: 992px) and (max-width: 1250px) {
    .menu {
        gap: 20px;
    }
    .header_outer img.logo {
        width: 100%;
    }
    .menu li {
        margin: 0;
    }
    .menu li a {
        font-size: 15px;
    }
    .dropdown {
        left: -60px;
    }
    
}