.login_container,
.register_container {
    background: #fff;
    border-radius: 10px;
    box-shadow: 0 0 23px 0 #00000021;
    padding: 30px;
    width: 50%;
    margin: auto;
}

.login_head,
.register_head {
    display: flex;
    justify-content: space-between;
    align-items: center;
    margin: 0 0 20px 0;
}

.login_content_heading h3,
.register_content_heading h3 {
    font-size: 24px;
    font-weight: 400;
    line-height: 24px;
    margin-bottom: 12px;
    text-align: left;
    font-family: League Spartan, sans-serif;
}

.login_content_heading p,
.register_content_heading p {
    font-size: 16px;
    line-height: 24px;
    font-family: League Spartan, sans-serif;
}

.login_heading_button a.login_back_navigation_btn,
.register_heading_button a.register_back_navigation_btn {
    color: #ae7d54;
    font-size: 16px;
    font-weight: 400;
    margin: 0;
    text-align: center;
    text-decoration: none;
    line-height: 24px;
    font-family: League Spartan, sans-serif;
}

.login_form_area,
.register_form_area {
    border-top: 1px solid #0000001a;
    padding-top: 18px;
}

.login_country_area,
.login_mobile_area,
.login_password_area,
.register_form_area .register_country_area {
    text-align: left;
    margin-bottom: 20px;
}

.login_country_area label,
.login_mobile_area label,
.login_password_area label {
    color: #666;
    font-size: 16px;
    font-weight: 400;
    line-height: 20px;
    margin-bottom: 15px;
    display: block;
    font-family: League Spartan, sans-serif;
}

.login_btn_area input.login_submit_btn,
.register_btn_area input.register_submit_btn {
    background: #ae7d54;
    border: 1px solid #0000;
    border-radius: 5px;
    color: #fff !important;
    font-size: 16px;
    font-weight: 600;
    width: 100%;
    padding: 12px 25px;
    text-align: center;
    text-transform: uppercase;
    transition: all .2s;
    cursor: pointer;
    font-family: League Spartan, sans-serif;
}

.login_already_account_area p,
.register_already_account_area p {
    justify-content: center;
    align-items: center;
    display: flex;
    font-size: 14px;
    gap: 5px;
    margin: 24px 0 0 0;
    font-family: League Spartan, sans-serif;
}

.login_already_account_area a.registe_login_link,
.register_already_account_area a.registe_login_link {
    align-items: center;
    color: #ae7d54;
    display: flex;
    font-size: 14px;
    gap: 8px;
    line-height: 20px;
    text-transform: capitalize;
    text-decoration: none;
    font-family: League Spartan, sans-serif;
}

.input_outer input,
.login_form_area select.login_country_input,
.register_form_area select.register_country_input {
    /* border: 1px solid #0000001a !important; */
    border-radius: 5px;
    border: 1px solid #f4f5f8;
    box-shadow: none !important;
    color: #686a6f !important;
    font-size: 16px;
    font-weight: 400;
    padding: 17px;
    width: 100%;
    font-family: League Spartan, sans-serif;
    background-color: #f4f5f8;
}
select.register_country_input:focus-visible {
    outline: none;
}

.password-field span.password-toggle {
    position: absolute;
    top: 50%;
    right: 20px;
    transform: translate(0px, -50%);
}

.password-field {
    position: relative;
}

.input_outer input,
select.login_country_input:focus {
    outline: none;
}

.input_outer input:focus,
.login_form_area select.login_country_input:focus,
.register_form_area select.register_country_input:focus {
    background-color: var(--bs-body-bg);
    border-color: #86b7fe;
}

.register_form_area label {
    display: block;
    text-align: left;
    color: #666;
    font-size: 16px;
    font-weight: 400;
    line-height: 20px;
    margin-bottom: 15px;
    font-family: League Spartan, sans-serif;
}

.register_form_area .input_outer {
    margin-bottom: 24px;
}

.register_agree_area label {
    margin: 0;
    padding-left: 20px;
}

.register_btn_area input.register_submit_btn {
    margin-top: 20px;
}

.register_agree_area label a {
    color: #ae7d54;
    text-decoration: none;
}

.register_form_area p.error-message,
.login_form_area p.error-message {
    color: #dc3545;
    display: block;
    text-align: left;
    font-size: 16px;
    font-family: League Spartan, sans-serif;
    font-weight: 400;
    margin-top: 10px;
}

.register_agree_area input.register_agree_check {
    position: absolute;
    left: 0;
    top: 4px;
}

.register_agree_area {
    position: relative;
}

.register_form_area .password-field svg,
.login_form_area .password-field svg {
    background: #0000;
    color: #90949c;
    cursor: pointer;
}

.register_outer,
.login_outer {
    position: relative;
    padding: 70px 0px;
}

.register_outer:before,
.login_outer:before {
    content: "";
    position: absolute;
    width: 100%;
    height: 100%;
    left: 0;
    right: 0;
    top: 0;
    bottom: 0;
    background: url(./../../assets/form_background.png) no-repeat scroll 50%;
    z-index: -1;
}


.header_rooter {
    display: flex;
    justify-content: space-between;
    align-items: center;
    max-width: 1320px;
    margin: auto;
    padding: 15px;
}

.header_buttons ul {
    display: flex;
    gap: 20px;
}

.header_buttons ul li {
    list-style: none;
}

.header_buttons ul li a {
    background: #ae7d54;
    border-radius: 10px;
    color: #fff;
    display: block;
    font-size: 20px;
    font-weight: 500;
    min-width: 140px;
    padding: 18px 35px;
    text-align: center;
    text-transform: uppercase;
    transition: all .2s;
    text-decoration: none;
    font-family: League Spartan, sans-serif;
}