.container {
    max-width: 1320px;
    margin: auto;
    padding: 0 15px;
}

.d_flex {
    display: flex;
    justify-content: space-around;
}

.book_perfect h2,
.home-card-title h2,
.dark_glossy_card_sec h2 {
    color: #121212;
    font-family: Lemon, sans-serif;
    font-size: 45px;
    font-weight: 400;
    margin-bottom: 50px;
    text-align: left;
}

.book_perfect span,
.home-card-title span,
.dark_glossy_card_sec span {
    color: #ae7d54;
    font-size: 45px;
    font-weight: 400;
}

.hero_outer {
    background-color: #fff;
}

.hero_outer p {
    color: #666;
    font-size: 20px;
    font-weight: 400;
    line-height: 150%;
    margin-bottom: 50px;
    text-align: left;
    font-family: League Spartan, sans-serif;
}

.hero_outer a.book_btn {
    background: #ae7d54;
    border-radius: 10px;
    color: #fff;
    display: block;
    font-size: 20px;
    font-weight: 500;
    min-width: 140px;
    padding: 18px 35px;
    text-align: center;
    text-transform: uppercase;
    transition: all .2s;
    width: fit-content;
    cursor: pointer;
    text-decoration: none;
}

.book_perfect {
    max-width: 40%;
}

.hero_outer_img {
    display: flex;
    justify-content: center;
}

.hero_outer_img img {
    max-width: 75%;
}

.hero_outer_bg_img:before {
    content: "";
    position: absolute;
    background: url(././../assets/hero_bg_img.png);
    height: 100%;
    left: -50px;
    right: 0;
    top: 0;
    bottom: 0;
    background-repeat: no-repeat;
    background-size: cover;
    margin: auto;
    width: 90%;
    z-index: -1;
}

.hero_outer_bg_img {
    position: relative;
    padding: 80px 0;
    z-index: 9;
}

.banner_section .banner_slide {
    display: flex;
    background: #fff;
    border-radius: 76px;
    justify-content: space-between;
    padding: 0 0 0 31px;
    width: 80%;
    align-items: center;
    gap: 50px;
    margin:10px auto auto auto;
    border: 1px solid #ae7d54;
}

.banner_section .banner_content {
    align-items: center;
    display: flex;
    justify-content: space-between;
    width: 80%;
}

.banner_search {
    background: #ae7d54;
    border-radius: 0 76px 76px 0;
    flex-direction: column;
    gap: 10px;
    padding: 33px 30px;
    display: flex;
    align-items: center;
}

.banner_box_heading h6 {
    color: #3d3b3a;
    font-size: 20px;
    font-weight: 400;
    font-family: League Spartan, sans-serif;
    margin-bottom: 10px;
}

.banner_box_heading p {
    color: #707070;
    font-size: 18px;
    font-weight: 300;
    margin-bottom: 0;
    font-family: League Spartan, sans-serif;
}

.banner_search h5 {
    color: #fff;
    font-size: 30px;
    font-weight: 500;
    margin-bottom: 0;
    text-transform: uppercase;
    font-family: League Spartan, sans-serif;
}

.banner_box {
    align-items: flex-start;
    border-left: 1px solid #0000001a;
    display: flex;
    gap: 10px;
    padding-left: 30px;
}

.banner_box:first-child {
    border: none;
}

.home-card-title h2 {
    text-align: center;
}

.home-card-title {
    padding: 40px 0 80px;
    max-width: 61%;
    margin: 0 auto;
}

.home-card-title p {
    text-align: center;
    margin: 0;
}

.card_images {
    display: flex;
    gap: 24px;
}


.home_facility {
    margin-top: -100px;
    padding: 60px 0;
    background: #aa84531a url(././../assets/facility_background.png) no-repeat scroll 100%;
    background-size: cover;
}

.home-facility_section {
    display: grid;
    grid-template-columns: 1fr 1fr 1fr;
    gap: 24px;
    margin-top: 100px;
}

.facility_content h4 {
    color: #121212;
    font-family: Lemon, sans-serif;
    font-size: 45px;
    font-weight: 400;
    padding-top: 10px;
}

.facility_content span {
    color: #ae7d54;
    font-size: 20px;
    font-weight: 500;
    font-family: League Spartan, sans-serif;
}

.facility_content {
    text-align: left;
}

.facility_content p {
    color: #666;
    font-size: 20px;
    font-weight: 400;
    line-height: 150%;
    padding-top: 40px;
    font-family: League Spartan, sans-serif;
}

.facility_section_thrd {
    align-items: center;
    border-bottom: 2px dotted #c0bfbe;
    display: flex;
    justify-content: space-between;
    padding: 20px;
}

.facility_title p {
    color: #666;
    font-size: 20px;
    font-weight: 400;
    margin-bottom: 12px;
    padding-top: 0;
    text-align: left;
    font-family: League Spartan, sans-serif;
}

.facility_title h5 {
    color: #121212;
    font-size: 24px;
    font-weight: 500;
    margin-bottom: 0;
    font-family: League Spartan, sans-serif;
}

.section_thrd_content {
    border: 1px solid #ae7d54;
    border-radius: 5px;
    padding: 5px 20px 4px;
}

.section_thrd_content h6 {
    color: #ae7d54;
    font-size: 24px;
    font-weight: 500;
    margin-bottom: 0;
    font-family: League Spartan, sans-serif;
}

.section_thrd_content span {
    color: #ae7d54;
    font-size: 20px;
    font-weight: 500;
}

.facility_image img {
    border: 6px solid #3d3b3a;
    border-radius: 15px;
    position: relative;
    z-index: 99;
}

.facility_price {
    margin-top: 60px;
}

/* All the Essentials*/

.comfortable_stay {
    display: flex;
    justify-content: space-between;
    padding: 70px 0;
    gap: 30px;
    align-items: center;
}

.facility_point {
    display: grid;
    grid-template-columns: 1fr 1fr;
    gap: 0 30px;
}

.facility_point .location_sec {
    align-items: center;
    display: flex;
    gap: 30px;
    padding-top: 70px;
}

.facility_point .location_sec:first-child {
    padding-top: 50px;
}

.facility_point .location_sec:nth-child(2) {
    padding-top: 50px;
}

.location_content h5 {
    color: #3d3b3a;
    font-size: 24px;
    font-weight: 600;
    font-family: League Spartan, sans-serif;
    text-align: left;
    margin: 0 0 8px;
}

.location_content p {
    color: #666;
    font-size: 18px;
    font-weight: 400;
    line-height: 133%;
    margin-bottom: 0;
    font-family: League Spartan, sans-serif;
    text-align: left;
}

.location_img img {
    max-width: 227px;
}

.dark_glossy_card_sec {
    padding: 0 50px;
}


section.get_in_touch {
    position: relative;
    z-index: 1;
}

/* GET IN TOUCH */
section.get_in_touch:before {
    background: #aa84531a url(././../assets/facility_background.png) no-repeat 100%;
    color: #121212;
    content: "Get In Touch";
    display: block;
    font-family: Lemon, sans-serif;
    font-size: 45px;
    font-weight: 400;
    height: 500px;
    padding-top: 50px;
    position: absolute;
    text-align: center;
    width: 100%;
    z-index: -1;
}

.get_touch_sec {
    background: linear-gradient(180deg, #ebd9c2, #fff);
    border-radius: 20px;
    margin: auto;
    padding: 50px 100px;
    width: 60%;
    position: relative;
    top: 200px;
}

.get_text {
    align-items: center;
    display: flex;
    justify-content: space-between;
}

.get_touch_content p,
.get_text span {
    color: #666;
    font-size: 20px;
    font-weight: 400;
    line-height: 180%;
    margin-bottom: 0;
    font-family: League Spartan, sans-serif;
    position: relative;
}

.get_text span:first-child:after {
    content: ":";
    left: 300px;
    position: absolute;
}

.get_touch_content p {
    margin-bottom: 40px;
}

.get_in_text h5 {
    color: #3d3b3a;
    font-size: 40px;
    font-weight: 600;
    line-height: 75%;
    font-family: League Spartan, sans-serif;
}

.get_in_text p {
    color: #666;
    font-size: 20px;
    font-weight: 400;
    line-height: 180%;
    text-align: left;
    font-family: League Spartan, sans-serif;
}

.get_in_devan {
    align-items: center;
    display: flex;
    justify-content: space-between;
    margin-top: 50px;
}

.get_in_devan a.trip_button {
    color: rgb(255, 255, 255);
    display: block;
    font-size: 20px;
    font-weight: 500;
    min-width: 140px;
    text-align: center;
    text-transform: uppercase;
    background: #ae7d54;
    border-width: 1px;
    border-image: initial;
    border-radius: 10px;
    padding: 12px 35px;
    transition: 0.2s;
    text-decoration: none;
    cursor: pointer;
}



@media (max-width: 767px) {
    .d_flex {
        display: block;
    }

    .book_perfect {
        max-width: 100%;
        padding: 0 0 50px;
    }

    .hero_outer a.book_btn {
        margin: auto;
    }

    .book_perfect h2,
    .home-card-title h2,
    .dark_glossy_card_sec h2 {
        font-size: 28px;
        text-align: center;
        margin-bottom: 20px;
    }

    .book_perfect span,
    .home-card-title span,
    .dark_glossy_card_sec span {
        font-size: 28px;
    }

    .hero_outer p {
        font-size: 18px;
        margin-bottom: 20px;
        text-align: center;
    }

    .facility_point {
        grid-template-columns: 1fr;
    }

    .comfortable_stay {
        display: block;
    }

    .dark_glossy_card_sec {
        padding: 0 5px;
    }

    .facility_point .location_sec {
        padding-top: 30px;
    }

    .location_sec img {
        max-width: 30px;
        height: auto;
    }

    section.get_in_touch:before {
        font-size: 28px;
    }

    .get_touch_sec {
        padding: 30px;
        top: 120px;
        width: 100%;
    }

    .get_touch_content p {
        margin-bottom: 20px;
    }

    .get_touch_content p,
    .get_text span {
        font-size: 14px;
    }

    .get_in_devan {
        display: block;
    }

    .get_in_text h5 {
        font-size: 28px;
        margin-bottom: 10px;
    }

    .get_in_text p {
        font-size: 14px;
        text-align: center;
    }

    .get_in_devan a.trip_button {
        font-size: 20px;
        max-width: 140px;
        margin: auto;
    }

    .home-facility_section {
        display: block;
    }

    .facility_image img {
        width: 100%;
    }

    .card_images {
        flex-direction: column;
    }

    .card_images .card_img img {
        width: 100%;
        margin-bottom: 15px;
    }

    .banner_section .banner_content {
        align-items: center;
        display: flex;
        flex-wrap: wrap;
        justify-content: center;
        width: 100%;
        gap: 20px;
        margin-bottom: 20px;
    }

    .banner_section .banner_slide {
        display: block;
        border-radius: 18px;
        padding: 20px;
        width: 100%;
    }

    .banner_content .banner_box {
        border-left: none;
        gap: 10px;
        padding-left: 20px;
    }

    .banner_section .banner_search {
        border-radius: 20px;
        gap: 10px;
        padding: 15px 0px;
        align-items: center;
        justify-content: center;
        max-width: 250px;
        margin: auto;
        flex-direction: row;
    }

    .home-card-section .home-card-title {
        padding: 40px 0 60px;
        max-width: 100%;
        margin: 0 auto;
    }

    .location_img img {
        max-width: 100%;
    }

    .location_img {
        margin-bottom: 20px;
    }

    .facility_content p {
        font-size: 18px;
        padding-bottom: 20px;
        padding-top: 30px;
    }

    .facility_content h4 {
        font-size: 28px;
        padding-top: 10px;
    }

}

@media (max-width: 1024px) {
    .d_flex {
        flex-direction: column;
    }

    .book_perfect {
        max-width: 100%;
        padding-bottom: 50px;
    }

    .book_perfect h2,
    .home-card-title h2,
    .dark_glossy_card_sec h2 {
        text-align: center;
    }

    .hero_outer a.book_btn {
        margin: auto;
    }

    .hero_outer p {
        text-align: center;
    }

    .hero_outer_bg_img:before {
        opacity: 0;
    }

    .banner_section .banner_slide {
        width: 100%;
        flex-direction: column;
        border-radius: 20px;
        padding: 20px;
    }

    .banner_search {
        flex-direction: row;
        padding: 13px 20px;
    }

    .banner_section .banner_content {
        width: 100%;
    }

    .banner_box {
        border-left: none;
    }

    .home-card-section .home-card-title {
        max-width: 100%;
    }

    .card_images {
        flex-direction: column;
    }

    .banner_slide .banner_search {
        flex-direction: row;
        padding: 15px 20px;
        border-radius: 15px;
    }

    .home-facility_section {
        grid-template-columns: 1fr;
    }

    .comfortable_stay {
        flex-direction: column;
    }

    .location_img img {
        max-width: 100%;
    }

    .get_touch_sec {
        width: 100%;
    }
}

@media (min-width: 1025px) and (max-width: 1250px) {
    .banner_box {
        padding-left: 20px;
    }

    .home-card-section .card_img img {
        width: 100%;
    }
}