.wallet_btn_grp {
    display: flex;
    gap: 30px;
    justify-content: center;
    margin-top: 50px;
    margin-bottom: 50px;
}

.wallet_btn_grp button {
    border-radius: 30px;
    color: #fff;
    font-size: 24px;
    font-weight: 700;
    line-height: 24px;
    padding: 20px 60px;
    transition: .3s;
    transition: all .2s;
