}

.wallet_btn_grp button.withdraw_btn {
    background: linear-gradient(180deg, #fcdb8f, #fcb716);
    border: 1px solid #b78718;
    box-shadow: inset 0 5px 5px 0 #b78718, 0 4px 51px 1px #00000040, inset 0 -5px 0 0 #b78718;
}

.wallet_selector select {
    border: 1px solid #0000001a;
