/* Help page */
.accordion-container {
    margin-top: 50px;
}

.accordion-item {
    border: 1px solid #ddd;
    border-radius: 0;
    margin-bottom: 10px;
    overflow: hidden;
}

.accordion-header {
    width: 100%;
    padding: 16px 20px;
    font-size: 16px;
    background: #fff;
    border: none;
    cursor: pointer;
    display: flex;
    justify-content: space-between;
    align-items: center;
    transition: background 0.3s ease;
    font-family: League Spartan, sans-serif;
    color: #212529;
