footer.footer  {
    position: relative;
    top: 200px;
    background: #fff url(../assets/image_footer.png) no-repeat scroll 100%;
    color: #8f8f8f;
    padding: 20px 12px;
}
.footer_panel p {
    color: #3d3b3a;
    font-size: 20px;
    font-weight: 400;
    line-height: 180%;
    text-align: center;
    font-family: League Spartan, sans-serif;
    margin-bottom: 20px;
}
.footer_panel p span {
    color: #ae7d54;
}

@media (max-width: 767px) {
.footer_panel p {
    font-size: 18px;
}
}